html {
  background-color: #f9f9f9;
}
#root {
  color: #000;
  font-family: 'Libre Baskerville', serif;
  font-size: 16px;
  padding: 10px;
}
h1,
h2 {
  font-family: 'Libre Franklin', sans-serif;
  font-weight: 700;
  letter-spacing: 4px;
}
h3,
h4,
h5 {
  font-family: 'Libre Franklin', sans-serif;
  font-weight: 700;
  letter-spacing: 2px;
}
.page-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.page-list .list-container {
  max-width: 960px;
}
.hero-container {
  border-bottom: 1px dotted #d10a92;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0;
  margin-top: 50px;
  width: 100%;
}
.footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.nav-container {
  border-bottom: 5px solid #d10a92;
  font-family: 'Libre Franklin', sans-serif;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.nav-container:hover {
  border-bottom: 5px solid #4515f0;
}
.nav-container ul {
  -ms-flex-align: stretch;
      align-items: stretch;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  font-weight: 600;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  letter-spacing: 2px;
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center;
  text-transform: uppercase;
}
.nav-container ul a {
  background-color: #4515f0;
  color: #fff;
  -ms-flex-positive: 1;
      flex-grow: 1;
  text-decoration: none;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.nav-container ul a:hover {
  background-color: #d10a92;
}
.nav-container ul li {
  padding: 20px;
}
.list-item {
  display: grid;
  grid-column-gap: 10px;
  grid-template-columns: 16px auto;
  line-height: 1.5rem;
  margin-bottom: 10px;
}
.list-item input {
  -ms-flex-item-align: start;
      align-self: start;
  margin-top: 6px;
  opacity: 1;
  -webkit-transition: opacity 0.3s ease;
  -o-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}
.list-item input:checked {
  opacity: 0.5;
}
.list-item input:checked+label {
  opacity: 0.5;
  text-decoration: line-through;
}
.list-item label {
  -ms-flex-item-align: start;
      align-self: start;
  opacity: 1;
  -webkit-transition: opacity 0.3s ease;
  -o-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}



